<template>
  <div class="text-center">
    <v-dialog v-model="status" width="1000">
      <v-card>
        <v-card-title
          class="headline"
          style="background-color: #25425e; color: white"
        >
          {{ funtions.name }}
        </v-card-title>

        <v-container class="pa-4 text-center">
          <template v-for="(data, i) in funtions.optservices">
            <v-row :key="i" class="fill-height" align="center" justify="center">
              <v-col cols="3" md="3" sm="12">
                <v-hover v-slot:default="{ hover }">
                  <v-card
                    :elevation="hover ? 12 : 2"
                    :class="{ 'on-hover': hover }"
                  >
                    <v-img
                      :src="require('@/assets/additional/asesoria/1.png')"
                      width="100%"
                    >
                    </v-img>
                  </v-card>
                </v-hover>
              </v-col>
              <v-col cols="8" md="8" sm="12">
                <v-row align="center" justify="center" class="pb-6">
                  <v-header>
                    <h3 v-text="data.name"></h3>
                  </v-header>
                </v-row>
                <v-row align="center" class="pt-6 text-justify">
                  <v-card-text
                    style="white-space: pre-line"
                    v-text="data.description"
                  ></v-card-text>
                </v-row>
                <v-row align="center" justify="center" class="pa-8">
                  <v-btn
                    class="ma-2"
                    tile
                    outlined
                    color="primary"
                    @click="
                    form.service=funtions.name;
                      form.subservice = data.name;
                      dialog = !dialog;
                    "
                  >
                    <v-icon left>mdi-plus-circle</v-icon> Asesoría
                  </v-btn>
                </v-row>
              </v-col>
            </v-row>
          </template>
        </v-container>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="status = false">
            <v-icon left>mdi-close</v-icon>
            Cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog" width="400px">
      <v-card>
        <v-card-title
          prepend-icon="mdi-user-outline"
          class="primary"
          style="color: white"
        >
          <v-icon color="white" left>mdi-account-box-multiple</v-icon>
          Formulario de Solicitud</v-card-title
        >
        <v-container>
          <v-row class="mx-2">
            <v-col class="align-center justify-space-between" cols="12">
              <v-row align="center" class="mr-0">
                <v-text-field
                  prepend-icon="mdi-account-details-outline"
                  placeholder="Nombre"
                  v-model="form.name"
                />
                <v-text-field
                  prepend-icon="mdi-account-outline"
                  placeholder="Apellido"
                  v-model="form.lastname"
                />
                <v-text-field
                  prepend-icon="mdi-whatsapp"
                  placeholder="Whatsapp"
                  v-model="form.phone"
                />
                <v-text-field
                  prepend-icon="mdi-email-outline"
                  placeholder="Correo"
                  v-model="form.email"
                />
              </v-row>
              <v-row align="center" class="pt-6">
                <v-subheader>
                  <v-icon left>mdi-information-variant</v-icon>
                  Dejanos tus datos que un asesor te contactará para un servicio
                  personalizado requerido.
                </v-subheader>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
        <v-card-actions>
          <v-btn text color="primary" @click="dialog = false">
            <v-icon left>mdi-reply</v-icon>

            Regresar</v-btn
          >
          <v-spacer />
          <v-btn text color="success" @click="onSubmit()">
            <v-icon left>mdi-loupe</v-icon>

            Enviar</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
export default {
  components: {},
  props: ["service", "status"],
  mounted() {},
  data() {
    return {
      form: {
        name: "",
        lastname: "",
        phone: "",
        email: "",
        service: "",
        subservice: "",
      },
      url: "/request",
      funtions: this.service,
      dialog: false,
      icons: ["mdi-rewind", "mdi-play", "mdi-fast-forward"],
      transparent: "rgba(255, 255, 255, 0)",
    };
  },
  methods: {
    datachange() {
      alert("cxc");
    },
    onSubmit() {
      this.$http({
        method: "post",
        url: this.url,
        data: this.form,
      })
        .then((res) => {
          if (res.data) {
            if (res.data.status === "success") {
              this.$alert.ok(res.data.message);
              this.dialog = false;
            }
            if (res.data.status === "empty") {
              this.$alert.info(res.data.message);
            }
            if (res.data.status === "fail") {
              this.$alert.err(res.data.message);
            }
          }
        })
        .catch((err) => {
          this.$alert.ok(err.message);
        })
        .finally(() => {
          this.loader = false;
        });
    },
  },
};
</script>
