<template>
  <v-container class="fill-height" fluid>
    <v-row align="center" justify="center">
      <v-col style="text-align: center">
        <v-card-title>
          <span style="text-shadow: 1px 1px 1px black; margin-right: 5px">
            <v-avatar color="secondary">
              <v-icon dark>mdi-toolbox</v-icon>
            </v-avatar>
            NUESTROS SERVICIOS
          </span>
        </v-card-title>
        <v-card flat tile>
          <v-row>
            <v-col
              v-for="(servicio, i) in services"
              :key="i"
              cols="12 "
              sm="6 "
              md="4 "
            >
              <v-card class="mx-auto" max-width="400">
                <v-img
                  gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                  class="white--text align-end"
                  height="200px"
                  opacity="0.2;"
                  :src="require(`@/assets/services/service-${i + 1}.png`)"
                >
                  <v-card-title>{{ servicio.name }}</v-card-title>
                </v-img>
                <v-card-text class="text--primary text-justify">
                  <div align-center>{{ servicio.description }}</div>
                </v-card-text>
                <v-card-actions>
                  <v-btn
                    color="primary"
                    @click="showval(servicio.name, servicio.optservices)"
                    text
                  >
                    <v-icon left> mdi-eye </v-icon>
                    Ver mas
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-card>
        <Aditionals :service="service" :status="status"></Aditionals
        ><!--Variable de un servicio en especifico-->
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Aditionals from "@/components/Services/Aditionals";
export default {
  components: {
    Aditionals,
  },
  data: () => ({
    status: false,
    service: {
      name: "",
      optservices: [
        {
          active: true,
          name: "",
          description: "",
        },
      ],
    },
    services: [],
  }),
  mounted() {
    this.getServices();
  },
  methods: {
    showval(service, optservices) {
      this.status = !this.status;
      this.service.name = service;
      this.service.optservices = optservices;
    },
    getServices() {
      this.$http
        .get(`/listar-serviciosweb`)
        .then((res) => {
          console.log("datas: ", res.data.services);
          if (res.data.services != null) {
            this.services = res.data.services;
          }
        })
        .finally();
    },
    getImage() {
      const min = 550;
      const max = 560;

      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
  },
};
</script>
